import React from 'react';
import logo from '../../assets/img/logo.svg';
import Greetings from '../../containers/Greetings/Greetings';
import './Popup.css';

class Popup extends React.Component {

  constructor(props) {
    super(props);
    window.addEventListener('load', this.getVideoData.bind(this));
    this.state = {
      duration: null,
      pageHasVideo: false,
    };
  }

  getVideoData() {
    var that = this;
    chrome.tabs.query({active: true, currentWindow: true}, (tabs) => {
        chrome.tabs.sendMessage(tabs[0].id, {type: "getDuration"}, (duration) => {
          if(typeof duration == "undefined") {
              // That's kind of bad
              if(chrome.runtime.lastError) {
                  // We couldn't talk to the content script, probably it's not there
                  console.log("Content script not found!!!");
              }
          } else {
              // Use duration
              that.setState({duration: duration});
          }
        });

        chrome.tabs.sendMessage(tabs[0].id, {type: "pageHasVideo"}, (pageHasVideo) => {
          if(typeof pageHasVideo == "undefined") {
              // That's kind of bad
              if(chrome.runtime.lastError) {
                  // We couldn't talk to the content script, probably it's not there
                  console.log("Content script not found!!!");
              }
          } else {
              that.setState({pageHasVideo: pageHasVideo});
          }
        });
    });
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Does the page you're on have a video? {this.state.pageHasVideo.toString()}. Also, the duration for the video is {this.state.duration}.
          </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React!
          </a>
        </header>
      </div>
    );
  }
}



export default Popup
